










// 效果见http://localhost:9200/test/dynamic-form-vue-test
import { defineComponent, ref } from '@vue/composition-api';
import Jsonschema2Form, {
  NumberType,
  SelectType,
  SelectType2,
  StringType,
  IntegerType,
} from '@4dst-saas/form-utils/dist/components/jsonschema2form';
import Jsonschema from '@4dst-saas/form-utils/dist/components/jsonschema2form/JSONschema';
import { VNode } from 'vue/types/vnode';
import antaJson from './AntaFormJson.json';

const formTest = defineComponent({
  components: {
    Jsonschema2Form,
    NumberType,
    SelectType,
    StringType,
    IntegerType,
  },
  setup(_, { root: { $set } }) {
    // const sel2 = ref<string | null>(null);
    // eslint-disable-next-line consistent-return
    function formItemGenerator(
      this: InstanceType<typeof formTest>,
      jss: Jsonschema,
      form: { [key: string]: any },
      key: string,
      attrs: Partial<Jsonschema>,
      h = this.$createElement,
    ): VNode | undefined {
      if (jss.custom) {
        // let slot: VNode;
        switch (jss.customType) {
          case 'selector2': {
            // eslint-disable-next-line no-case-declarations
            const { baseString, from, to } = jss as Jsonschema & {
              baseString?: string,
              from: number,
              to: number
            };
            // eslint-disable-next-line no-case-declarations
            const options: string[] | number[] = [];
            if (baseString) {
              for (let i = from; i <= to; i++) {
                options.push(`${baseString}${i}`);
              }
            } else {
              for (let i = from; i <= to; i++) {
                options.push(i);
              }
            }
            // eslint-disable-next-line no-use-before-define
            return h(SelectType2, {
              props: { options, val: form[key] },
              // eslint-disable-next-line
              on: {
                change: (s: string) => { $set(form, key, s); },
              },
            });
          }
          default: return h('div', 'custom default');
        }
      }
      // 返回空或不返回会做默认处理
    }
    return {
      num: ref(null),
      sel: ref(null),
      str: ref(null),
      int: ref(null),
      antaJson,
      formItemGenerator,
    };
  },
});
export default formTest;
